.main-nav  {}

.main-nav__list {
  display: flex;
}

.main-nav__item {
  margin-right: 20px;

  &:last-child {
    margin-right: 0;
  }
}

.main-nav__link {
  font-size: 14px;
  line-height: 16px;
}

.main-nav--footer {
  .main-nav__link {
    color: @white;

    &:hover {
      opacity: 0.8;
    }

    &:active,
    &:focus {
      opacity: 0.6;
    }
  }
}
