.header {
  a {
    color: @black;

    &:hover {
      opacity: 0.8;
    }

    &:active,
    &:focus {
      opacity: 0.6;
    }
  }
}

.header__wrapper-top {
  background: linear-gradient(89.95deg, #FFE200 0.61%, #F59A00 100%);
}

.header__wrapper-top-inner {
  max-width: 1680px;
  padding-left: 80px;
  padding-right: 80px;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 12px;
  padding-bottom: 12px;
}

.header__top-nav {
  margin-left: auto;
}

.header__swirch {
  width: 25px;
  height: 14px;
  background-color: blue;
  margin-left: 40px;
}

.header__wrapper-bottom {
  max-width: 1680px;
  padding-left: 80px;
  padding-right: 80px;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-top: 19px;
  padding-bottom: 20px;
}

.header__logo {
  margin-right: 20px;
  margin-left: 20px;
}

.header__tel {
  // transform: translateX(calc(-100% - 70px));
  font-size: 20px;
  line-height: 24px;
  font-weight: 500;
}
