.user-list {
  display: flex;
  align-items: center;
}

.user-list__item {
  margin-right: 20px;

  &:last-child {
    margin-right: 0;
    min-width: 46px;
  }
}

.user-list__link-count {
  display: inline-block;
  vertical-align: top;
  background-image: @gradient;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin-top: 4px;
  padding-left: 7px;
  padding-top: 2px;
  font-size: 12px;
}
