.subscription {
  background-color: @gray;
}

.subscription__wrapper {
  padding-top: 120px;
  padding-bottom: 120px;
}

.subscription__slogan {
  .slogan();
}

.subscription__bold {
  display: block;
  .small-title();
  margin-bottom: 80px;
}

.subscription__input {
  background-color: transparent;
  border: none;
  font-size: 16px;
  line-height: 24px;
  font-family: @jost;
  color: @black;
  flex-grow: 1;
  outline: none;

  &::placeholder {
    border: none;
    font-size: 16px;
    line-height: 24px;
    font-family: @jost;
    color: @black;
  }
}

.subscription__form {
  width: 100%;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: space-between;
  padding-bottom: 7px;
  border-bottom: 2px solid rgba(52, 57, 59, 0.2);
}

.subscription__submit {
  padding-right: 0;
  padding-left: 0;
  border: none;
  font-size: 20px;
  line-height: 29px;
  font-weight: 500;
  color: transparent;
  background-clip: text;
  background-image: @gradient;
  cursor: pointer;

  &:hover {
    opacity: 0.8;
  }

  &:active,
  &:focus {
    opacity: 0.6;
  }
}
