.presentation__wrapper {
  padding-top: 20px;
  padding-bottom: 100px;
}

.presentation__list {
  display: grid;
  gap: 20px;
  grid-template-columns: 632px 1fr;
  grid-auto-rows: minmax(285px, auto);
  justify-items: stretch;
  align-items: stretch;

  .presentation__item {
    margin-bottom: 0;
    min-height: 285px;
    background-color: @gray;
    position: relative;

    &:nth-child(1) {
      grid-column-start: 1;
      grid-column-end: 2;
      grid-row-start: 1;
      grid-row-end: 3;

      .presentation__item-wrapper {
        min-height: 590px;
      }
    }

    &:nth-child(2) {
      grid-column-start: 2;
      grid-column-end: 3;
      grid-row-start: 1;
      grid-row-end: 2;
      background-color: yellow;
    }

    &:nth-child(3) {
      grid-column-start: 2;
      grid-column-end: 3;
      grid-row-start: 2;
      grid-row-end: 3;
      background-color: yellowgreen;
    }

    &:nth-child(4) {
      grid-column-start: 1;
      grid-column-end: 2;
      grid-row-start: 3;
      grid-row-end: 4;
    }

    &:nth-child(5) {
      grid-column-start: 1;
      grid-column-end: 2;
      grid-row-start: 4;
      grid-row-end: 5;
    }

    &:nth-child(6) {
      grid-column-start: 2;
      grid-column-end: 3;
      grid-row-start: 3;
      grid-row-end: 5;

      .presentation__item-wrapper {
        min-height: 590px;
      }
    }

    &:nth-child(7) {
      grid-column-start: 1;
      grid-column-end: 2;
      grid-row-start: 5;
      grid-row-end: 7;

      .presentation__item-wrapper {
        min-height: 590px;
      }
    }

    &:nth-child(8) {
      margin-bottom: 0;
      grid-column-start: 2;
      grid-column-end: 3;
      grid-row-start: 5;
      grid-row-end: 6;
    }

    &:nth-child(9) {
      grid-column-start: 2;
      grid-column-end: 3;
      grid-row-start: 6;
      grid-row-end: 7;
    }
  }
}

.presentation__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}

.presentation__item-wrapper {
  min-height: 285px;
  width: 100%;
  position: relative;
  z-index: 2;
  padding: 52px;
}

.presentation__small-txt {
  font-size: 16px;
  line-height: 19px;
  color: @black;
}

.presentation__item-title {
  font-family: @jost;
  font-size: 28px;
  line-height: 40px;
  font-weight: 500;
  margin-top: 0;
  margin-bottom: 32px;
}

.presentation__title-link {
  color: @black;

  &:hover {
    opacity: 0.8;
  }

  &:active,
  &:focus {
    opacity: 0.6;
  }
}

.presentation__link {
  font-size: 16px;
  line-height: 19px;
  display: inline-block;
  padding-right: 44px;
  position: relative;
  color: @black;
  z-index: 2;

  &::after {
    content: "";
    width: 32px;
    height: 2px;
    display: block;
    background: @gradient;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
  }
}

.presentation__popup {
  z-index: 3;

  &--item-1 {
    top: 30%;
    right: 30%;
  }

  &--item-2 {
    top: 10%;
    right: 55%;
  }

  &--item-3 {
    top: 15%;
    right: 30%;
  }

  &--item-4 {
    top: 14%;
    right: 55%;
  }

  &--item-5 {
    top: 15%;
    right: 50%;
  }

  &--item-6 {
    top: 60%;
    right: 30%;
  }

  &--item-7 {
    top: 35%;
    right: 15%;
  }

  &--item-8 {
    top: 35%;
    right: 40%;
  }

  &--item-9 {
    top: 9%;
    right: 62%;
  }
}
