.products-list {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
}

.products-list__item {
  margin-right: 20px;
  margin-bottom: 20px;
  position: relative;

  &:nth-child(4n) {
    margin-right: 0;
  }
}

.products-list__sale-count {
  display: none;
}

.products-list__item--sale {
  .products-list__sale-count {
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    display: inline-block;
    vertical-align: middle;
    padding: 6px 14px;
    background: @gradient;
    position: absolute;
    top: 0;
    left: 0;
  }
}

.products-list__item-wrapper-txt {
  text-align: center;
}

.products-list__title {
  margin-top: 16px;
  margin-bottom: 4px;
  font-family: @jost;
  font-weight: 500;
  font-size: 20px;
  line-height: 29px;
}

.products-list__link-title {
  color: @black;
}

.products-list__size {
  font-family: @jost;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}

.products-list__price {
  font-weight: 500;
  font-size: 28px;
  line-height: 32px;
}
