.plus-popup__btn {
  background-color: transparent;
  border: none;
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  padding: 0;
  height: 22px;
  width: 22px;
}

.plus-popup {
  position: absolute;
  min-width: 22px;
  min-height: 22px;

  &:hover {
    transform: translateX(calc(100% - 21.5px));
    padding-left: 28px;
    left: calc(37% - 30px);
  }

  &:hover .plus-popup__txt {
    display: block;
  }
}

.plus-popup__txt {
  display: none;
  padding-left: 40px;
  margin-left: -40px;

  p {
    width: 400px;
    padding: 30px 20px;
    background-color: @black;
    color: @white;
  }
}
