* {
  box-sizing: border-box;
}
body {
  padding: 0;
  margin: 0;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #34393B;
}
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
p {
  margin: 0;
}
.vis-hidd {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}
a {
  text-decoration: none;
  transition: all ease-in 0.3s;
  outline: none;
}
button {
  outline: none;
}
.container {
  max-width: 1324px;
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;
}
.small-title {
  font-family: "Jost", sans-serif;
  font-weight: 500;
  font-size: 40px;
  line-height: 58px;
  margin-top: 0;
  margin-bottom: 0;
  text-align: center;
}
.header a {
  color: #34393B;
}
.header a:hover {
  opacity: 0.8;
}
.header a:active,
.header a:focus {
  opacity: 0.6;
}
.header__wrapper-top {
  background: linear-gradient(89.95deg, #FFE200 0.61%, #F59A00 100%);
}
.header__wrapper-top-inner {
  max-width: 1680px;
  padding-left: 80px;
  padding-right: 80px;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 12px;
  padding-bottom: 12px;
}
.header__top-nav {
  margin-left: auto;
}
.header__swirch {
  width: 25px;
  height: 14px;
  background-color: blue;
  margin-left: 40px;
}
.header__wrapper-bottom {
  max-width: 1680px;
  padding-left: 80px;
  padding-right: 80px;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-top: 19px;
  padding-bottom: 20px;
}
.header__logo {
  margin-right: 20px;
  margin-left: 20px;
}
.header__tel {
  font-size: 20px;
  line-height: 24px;
  font-weight: 500;
}
.main-nav__list {
  display: flex;
}
.main-nav__item {
  margin-right: 20px;
}
.main-nav__item:last-child {
  margin-right: 0;
}
.main-nav__link {
  font-size: 14px;
  line-height: 16px;
}
.main-nav--footer .main-nav__link {
  color: #ffffff;
}
.main-nav--footer .main-nav__link:hover {
  opacity: 0.8;
}
.main-nav--footer .main-nav__link:active,
.main-nav--footer .main-nav__link:focus {
  opacity: 0.6;
}
.bottom-nav {
  display: flex;
  align-items: center;
  margin-right: 50px;
}
.bottom-nav__list {
  display: flex;
}
.bottom-nav__item {
  margin-right: 20px;
}
.bottom-nav__item:last-child {
  margin-right: 0;
}
.bottom-nav__link {
  font-size: 20px;
  line-height: 24px;
  font-weight: 500;
}
.user-list {
  display: flex;
  align-items: center;
}
.user-list__item {
  margin-right: 20px;
}
.user-list__item:last-child {
  margin-right: 0;
  min-width: 46px;
}
.user-list__link-count {
  display: inline-block;
  vertical-align: top;
  background-image: linear-gradient(89.95deg, #FFE200 0.61%, #F59A00 100%);
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin-top: 4px;
  padding-left: 7px;
  padding-top: 2px;
  font-size: 12px;
}
.footer {
  background-color: #34393B;
}
.footer__wrapper {
  max-width: 1680px;
  padding-left: 80px;
  padding-right: 80px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 40px;
}
.soc-list {
  display: flex;
  align-items: center;
}
.soc-list__item {
  margin-right: 20px;
}
.soc-list__item:last-child {
  margin-right: 0;
}
.main-slider {
  min-height: 732px;
  background-color: #EAEAE8;
}
.main-slider__wrapper {
  min-height: 732px;
}
.main-slider__container {
  min-height: 732px;
}
.main-slider__slide {
  background-image: url("../img/main-img.jpg");
  background-size: cover;
  background-position: 50%;
  background-repeat: no-repeat;
  min-height: 732px;
  height: 100%;
}
.main-slider__slide-wrapper {
  margin-top: 260px;
  padding-bottom: 135px;
  padding-left: 60px;
}
.main-slider__title {
  margin-bottom: 40px;
}
.main-slider__popup {
  top: 10%;
  right: 37%;
}
.main-slider__pagination {
  bottom: 137px!important;
}
.main-slider__pagination .swiper-pagination-bullet {
  background-color: #ffffff;
  opacity: 1;
  outline: none;
}
.main-slider__pagination .swiper-pagination-bullet-active {
  background: linear-gradient(89.95deg, #FFE200 0.61%, #F59A00 100%);
}
.main-title {
  font-size: 62px;
  line-height: 90px;
  font-weight: 400;
  font-family: "Jost", sans-serif;
}
.btn {
  display: inline-block;
  padding: 22px 52px;
  background: linear-gradient(89.95deg, #FFE200 0.61%, #F59A00 100%);
  font-size: 16px;
  line-height: 19px;
  text-transform: uppercase;
  letter-spacing: 0.08em;
  color: #34393B;
}
.plus-popup__btn {
  background-color: transparent;
  border: none;
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  padding: 0;
  height: 22px;
  width: 22px;
}
.plus-popup {
  position: absolute;
  min-width: 22px;
  min-height: 22px;
}
.plus-popup:hover {
  transform: translateX(calc(100% - 21.5px));
  padding-left: 28px;
  left: calc(37% - 30px);
}
.plus-popup:hover .plus-popup__txt {
  display: block;
}
.plus-popup__txt {
  display: none;
  padding-left: 40px;
  margin-left: -40px;
}
.plus-popup__txt p {
  width: 400px;
  padding: 30px 20px;
  background-color: #34393B;
  color: #ffffff;
}
.presentation__wrapper {
  padding-top: 20px;
  padding-bottom: 100px;
}
.presentation__list {
  display: grid;
  gap: 20px;
  grid-template-columns: 632px 1fr;
  grid-auto-rows: minmax(285px, auto);
  justify-items: stretch;
  align-items: stretch;
}
.presentation__list .presentation__item {
  margin-bottom: 0;
  min-height: 285px;
  background-color: #EAEAE8;
  position: relative;
}
.presentation__list .presentation__item:nth-child(1) {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 3;
}
.presentation__list .presentation__item:nth-child(1) .presentation__item-wrapper {
  min-height: 590px;
}
.presentation__list .presentation__item:nth-child(2) {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 2;
  background-color: yellow;
}
.presentation__list .presentation__item:nth-child(3) {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 2;
  grid-row-end: 3;
  background-color: yellowgreen;
}
.presentation__list .presentation__item:nth-child(4) {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 3;
  grid-row-end: 4;
}
.presentation__list .presentation__item:nth-child(5) {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 4;
  grid-row-end: 5;
}
.presentation__list .presentation__item:nth-child(6) {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 3;
  grid-row-end: 5;
}
.presentation__list .presentation__item:nth-child(6) .presentation__item-wrapper {
  min-height: 590px;
}
.presentation__list .presentation__item:nth-child(7) {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 5;
  grid-row-end: 7;
}
.presentation__list .presentation__item:nth-child(7) .presentation__item-wrapper {
  min-height: 590px;
}
.presentation__list .presentation__item:nth-child(8) {
  margin-bottom: 0;
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 5;
  grid-row-end: 6;
}
.presentation__list .presentation__item:nth-child(9) {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 6;
  grid-row-end: 7;
}
.presentation__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}
.presentation__item-wrapper {
  min-height: 285px;
  width: 100%;
  position: relative;
  z-index: 2;
  padding: 52px;
}
.presentation__small-txt {
  font-size: 16px;
  line-height: 19px;
  color: #34393B;
}
.presentation__item-title {
  font-family: "Jost", sans-serif;
  font-size: 28px;
  line-height: 40px;
  font-weight: 500;
  margin-top: 0;
  margin-bottom: 32px;
}
.presentation__title-link {
  color: #34393B;
}
.presentation__title-link:hover {
  opacity: 0.8;
}
.presentation__title-link:active,
.presentation__title-link:focus {
  opacity: 0.6;
}
.presentation__link {
  font-size: 16px;
  line-height: 19px;
  display: inline-block;
  padding-right: 44px;
  position: relative;
  color: #34393B;
  z-index: 2;
}
.presentation__link::after {
  content: "";
  width: 32px;
  height: 2px;
  display: block;
  background: linear-gradient(89.95deg, #FFE200 0.61%, #F59A00 100%);
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}
.presentation__popup {
  z-index: 3;
}
.presentation__popup--item-1 {
  top: 30%;
  right: 30%;
}
.presentation__popup--item-2 {
  top: 10%;
  right: 55%;
}
.presentation__popup--item-3 {
  top: 15%;
  right: 30%;
}
.presentation__popup--item-4 {
  top: 14%;
  right: 55%;
}
.presentation__popup--item-5 {
  top: 15%;
  right: 50%;
}
.presentation__popup--item-6 {
  top: 60%;
  right: 30%;
}
.presentation__popup--item-7 {
  top: 35%;
  right: 15%;
}
.presentation__popup--item-8 {
  top: 35%;
  right: 40%;
}
.presentation__popup--item-9 {
  top: 9%;
  right: 62%;
}
.popular__wrapper {
  padding-top: 40px;
  padding-bottom: 80px;
}
.popular__slogan {
  font-family: "Jost", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
}
.popuplar__title {
  font-family: "Jost", sans-serif;
  font-weight: 500;
  font-size: 40px;
  line-height: 58px;
  margin-top: 0;
  margin-bottom: 0;
  text-align: center;
  margin-bottom: 40px;
}
.products-list {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
}
.products-list__item {
  margin-right: 20px;
  margin-bottom: 20px;
  position: relative;
}
.products-list__item:nth-child(4n) {
  margin-right: 0;
}
.products-list__sale-count {
  display: none;
}
.products-list__item--sale .products-list__sale-count {
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  display: inline-block;
  vertical-align: middle;
  padding: 6px 14px;
  background: linear-gradient(89.95deg, #FFE200 0.61%, #F59A00 100%);
  position: absolute;
  top: 0;
  left: 0;
}
.products-list__item-wrapper-txt {
  text-align: center;
}
.products-list__title {
  margin-top: 16px;
  margin-bottom: 4px;
  font-family: "Jost", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 29px;
}
.products-list__link-title {
  color: #34393B;
}
.products-list__size {
  font-family: "Jost", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}
.products-list__price {
  font-weight: 500;
  font-size: 28px;
  line-height: 32px;
}
.sales__wrapper {
  padding-top: 40px;
  padding-bottom: 126px;
}
.sales__slogan {
  font-family: "Jost", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
}
.sales__title {
  font-family: "Jost", sans-serif;
  font-weight: 500;
  font-size: 40px;
  line-height: 58px;
  margin-top: 0;
  margin-bottom: 0;
  text-align: center;
  margin-bottom: 40px;
}
.subscription {
  background-color: #EAEAE8;
}
.subscription__wrapper {
  padding-top: 120px;
  padding-bottom: 120px;
}
.subscription__slogan {
  font-family: "Jost", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
}
.subscription__bold {
  display: block;
  font-family: "Jost", sans-serif;
  font-weight: 500;
  font-size: 40px;
  line-height: 58px;
  margin-top: 0;
  margin-bottom: 0;
  text-align: center;
  margin-bottom: 80px;
}
.subscription__input {
  background-color: transparent;
  border: none;
  font-size: 16px;
  line-height: 24px;
  font-family: "Jost", sans-serif;
  color: #34393B;
  flex-grow: 1;
  outline: none;
}
.subscription__input::placeholder {
  border: none;
  font-size: 16px;
  line-height: 24px;
  font-family: "Jost", sans-serif;
  color: #34393B;
}
.subscription__form {
  width: 100%;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  justify-content: space-between;
  padding-bottom: 7px;
  border-bottom: 2px solid rgba(52, 57, 59, 0.2);
}
.subscription__submit {
  padding-right: 0;
  padding-left: 0;
  border: none;
  font-size: 20px;
  line-height: 29px;
  font-weight: 500;
  color: transparent;
  background-clip: text;
  background-image: linear-gradient(89.95deg, #FFE200 0.61%, #F59A00 100%);
  cursor: pointer;
}
.subscription__submit:hover {
  opacity: 0.8;
}
.subscription__submit:active,
.subscription__submit:focus {
  opacity: 0.6;
}
